import Vue from 'vue';
import Vuex from 'vuex';
import $axios from '../plugins/ajax';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    carTypeOptions: null,
    carColorOptions: null,
    token: '',
    userId: ''
  },
  mutations: {
    SET_CAR_TYPE_OPTIONS(state, value) {
      state.carTypeOptions = value;
    },
    SET_CAR_COLOR_OPTIONS(state, value) {
      state.carColorOptions = value;
    },
    SET_TOKEN(state, value) {
      state.token = value;
    },
    SET_USER_ID(state, value) {
      state.userId = value;
    }
  },
  actions: {
    LOGIN({ state, commit }, params) {
      return new Promise((resolve, reject) => {
        $axios.post('/login', params).then((res) => {
          if (res.data.result) {
            commit('SET_USER_ID', res.data.userId);
            commit('SET_TOKEN', res.data.userId);
            localStorage.setItem('token', res.data.userId);
            resolve(res.data);
          } else {
            reject(res.data.message);
          }
        });
      });
    },
    getCarTypeOptions({ state, commit }, params) {
      return new Promise((resolve, reject) => {
        $axios.get('/getCarTypeOptions').then((res) => {
          if (res.data.result) {
            commit('SET_CAR_TYPE_OPTIONS', res.data.carTypeOptions);
            resolve(res.data.message);
          } else {
            reject(res.data.message);
          }
        });
      });
    },
    getCarColorOptions({ state, commit }, params) {
      return new Promise((resolve, reject) => {
        $axios.get('/getCarColorOptions').then((res) => {
          if (res.data.result) {
            commit('SET_CAR_COLOR_OPTIONS', res.data.carColorOptions);
            resolve(res.data.message);
          } else {
            reject(res.data.message);
          }
        });
      });
    }
  }
});
