import axios from 'axios';

const AXIOS_OPTIONS = {
  // withCredentials: true,
  // baseURL: 'http://192.168.1.111:8000', // 本地开发
  baseURL: 'http://39.106.153.0:8001', // github发布
  timeout: 60000
};

export default {
  get(url, params, config) {
    let options = {};
    if (typeof config !== 'undefined') {
      options = Object.assign({}, AXIOS_OPTIONS, { params: params }, config);
    } else {
      options = Object.assign({}, AXIOS_OPTIONS, { params: params });
    }
    return axios.get(url, options);
  },
  post(url, data, config) {
    let options = {};
    if (typeof config !== 'undefined') {
      options = Object.assign({}, AXIOS_OPTIONS, config);
    } else {
      options = Object.assign({}, AXIOS_OPTIONS);
    }
    return axios.post(url, data, options);
  }
};
